	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://danmaq.com/image/photo/sunrise2009.jpg\"></p>\n<p><em>danmaqは2008年12月28日で4周年を迎えました。</em>(今まで29日だと思ってたが新たに昔のログが見つかって28日始動だったっぽい)去年は復活宣言しておきながら大した活動出来てなかったので今年こそdanmaqらしいdanmaq活動ができたらいいなと思います。</p>\n<h2>初日の出&#x26;初詣</h2>\n<p>去年は寝正月で除夜の鐘も初日の出も夢の中、初詣も正月の晩になってからだったので、今年は全部見てきましたよ。(除夜の鐘は単に日が変わるまでに大掃除が終わらなかっただけだったりするんですがorz)ちなみに帰ってからは毎年恒例の寝正月でした。誰か遊びに来い 'Ａ｀</p>\n<p>初詣は夜行列車に乗って<a href=\"/image/photo/kame.jpg\">いつものところ</a>へ。</p>\n<p>そしてものすごい参拝行列。今回夜行列車で日の出前の空がまだ真っ黒な状態で到着したので昨年比で相対的だと少ないんですがやっぱり異常。きっとこの列の向こうに大手サークルさんが居るんですね、分かります。(壁サークルとしては最弱小レベルで悪かったな)</p>\n<p>絵馬も書いてきましたよ。去年は<a href=\"/image/photo/ls2008.jpg\">痛いだけだった(おまけに手がパーフェクトフリーズしてド下手糞)</a>だったので、今年はガチで抱負書いてきました。さて今年の抱負はどこまで実現できるんだか。絵とプログラムの両刃使いなんて本当になれるものだろうか。しかしみんな絵うまいなぁ。貼り付けてるのはさておき、木に直接描いてるっぽいのでも滲んでないのも見かけるが、どうやって描いただろ。(コピックは泣きたくなるくらいに滲みまくりだぜ)</p>\n<p>初日の出は当初東京タワーかサンシャイン60にでも行こうかと思ったのですが、間に合いそうになかったので近所の公園で撮ってきました。なかなか、意外と絵になるなぁ。</p>","frontmatter":{"cover":null,"date":"2009-01-02T00:00:00+09:00","strDate":"2009/1/1","title":"謹賀新年"}}},"pathContext":{"path":"/ja/blog/2009/01/02-謹賀新年/","langKey":"ja"}}

